import React from 'react'

const Table = (props) => <table className='table'>{props.children}</table>

const Head = ({ children }) => <thead className='thead'>{children}</thead>
const Body = ({ children }) => <tbody className='tbody'>{children}</tbody>
const Row = ({ children }) => <tr className='tr'>{children}</tr>
const Th = ({ children, className }) => <th className={`th ${className}`}>{children}</th>
const Td = ({ children, className }) => <td className={`td ${className}`}>{children}</td>

const Foot = ({ children }) => <tfoot className='tbody'>{children}</tfoot>


Table.Head = Head
Table.Th = Th
Table.Body = Body
Table.Row = Row
Table.Td = Td
Table.Foot = Foot


export default Table




