import React, { useState } from 'react'
import useApiOpenAI from '../../../../Shared/services/api/useApiOpenAI'

const OpenAIDescriptionBtn = (props) => {
  const { apiOpenAI } = useApiOpenAI()
  const { formValues, changeVal } = props
  const jobTitle = formValues && formValues.title ? formValues.title : ''

  console.log('BTTT:', jobTitle)
  const [msg, setMsg] = useState('')
  const [loading, setLoading] = useState(false)

  const handleSubmit = async (e) => {
    e.preventDefault()
    setLoading(true)
    if (jobTitle == 'undefined' || jobTitle == '' || jobTitle == null) {
      setMsg('Please Enter the Job Title')
      console.log('Please Enter the Job Title')
      setLoading(false)
      setTimeout(() => {
        setMsg('')
      }, 6000)
      return
    }
    const resp = await apiOpenAI({
      prompt: [`Please write a short 5 lines job description of '${jobTitle}'`, `Please wrie a job requirements ${jobTitle}`],
      max_tokens: 200
    })

    changeVal('description', resp && resp.choices && resp.choices[0].text.trim())
    changeVal('requirements', resp && resp.choices && resp.choices[1].text.trim())
    setLoading(false)
  }

  return (
    <div className={`olivia_button ${loading && 'animate_olivia_button'}`}>
      <button onClick={handleSubmit}>
        {!loading && <svg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'>
          <g clip-path='url(#clip0_2888_667)'>
            <path d='M13.7033 2.76604L13.787 2.98939L14.0133 3.06482L14.4189 3.2L14.0133 3.33518L13.787 3.41061L13.7033 3.63396L13.2 4.976L12.6967 3.63396L12.613 3.41061L12.3867 3.33518L11.9811 3.2L12.3867 3.06482L12.613 2.98939L12.6967 2.76604L13.2 1.424L13.7033 2.76604ZM12.1033 12.366L12.187 12.5894L12.4133 12.6648L12.8189 12.8L12.4133 12.9352L12.187 13.0106L12.1033 13.234L11.6 14.576L11.0967 13.234L11.013 13.0106L10.7867 12.9352L10.3811 12.8L10.7867 12.6648L11.013 12.5894L11.0967 12.366L11.6 11.024L12.1033 12.366ZM6.2967 6.15736L6.37856 6.38657L6.60945 6.46353L8.81886 7.2L6.60945 7.93647L6.37856 8.01344L6.2967 8.24264L5.2 11.3134L4.1033 8.24264L4.02144 8.01344L3.79055 7.93647L1.58114 7.2L3.79055 6.46353L4.02144 6.38657L4.1033 6.15736L5.2 3.08661L6.2967 6.15736Z' fill='white' stroke='#C8970F' />
          </g>
          <defs>
            <clipPath id='clip0_2888_667'>
              <rect width='16' height='16' fill='white' />
            </clipPath>
          </defs>
        </svg>}
        {loading ? <div>Olivia <div class='dots' /></div> : 'Write with Olivia'}
        <span />
        <span />
        <span />
        <span />
        <span />
      </button>
      {msg && <p>{msg}</p>}
    </div>
  )
}

export default OpenAIDescriptionBtn



