export const getScoreInfo = (data, candidateId, jobId) => {

  if (!data || !candidateId || !jobId) return
  const hasAtsData = data[jobId]
  if (!hasAtsData) return

  const res = data && data[jobId].find(item => item.candidateId == candidateId)

  return res
}
// const applicantIds = applications.map( a => a.id)

export const scoreToFixed = (scoreValue, toFixedLimit = 2) => {
  const res = parseFloat(scoreValue).toFixed(toFixedLimit)
  return res
}

export const mergeAtsList = (arr1, arr2) => {
  const result = {}

  for (const key in arr1) {
    result[key] = arr1[key].map(item => ({ ...item }))
  }

  for (const key in arr2) {
    if (!result[key]) {
      result[key] = []
    }

    arr2[key].forEach(item => {
      const existingItem = result[key].find(i => i.candidateId === item.candidateId)

      if (existingItem) {
        Object.assign(existingItem, item)
      } else {
        result[key].push({ ...item })
      }
    })
  }
  return result
}

export const mergedAtsMatchListWithAlgolia = (array1, array2, jobId) => {
  if (!array1) return // console.log('Array 1 not exist')
  if (!array2) return // console.log('Array 2 not exist')
  if (!jobId) return // console.log('jobId not exist')

  const jobArrayIndex = Object.keys(array2).indexOf(jobId)
  const jobArrayObj = Object.keys(array2)

  if (!jobArrayIndex) return
  if (!jobArrayObj) return

  const mergedResp = array1 && array1.map(obj => {
    const a1Ref = obj.id
    const arr2Obj = (array2 && array2[jobArrayObj[jobArrayIndex]]) && array2[jobArrayObj[jobArrayIndex]].find(tmp => tmp.candidateId === a1Ref);
    if (arr2Obj) { return { ...obj, score: arr2Obj.score } } else { return {...obj, score: '-1'} }
  })

  return mergedResp
}



