import { postRequest } from './commonApi'
import { endPoints } from './endPoints'

const useApiOpenAI = () => {
  const apiOpenAI = async (data, json, header) => {
    const url = endPoints.openAIResponse
    // headers: {
    //     'Content-Type': 'application/json',
    //     Authorization: `Bearer ${apiKey}`
    // }
    try {
      let res = await postRequest(url, data, json = true, header = {Authorization: `Bearer ${process.env.REACT_APP_OLIV_OPEN_AI_API}`})

      if (res) {
        return res
      }
    } catch (err) {
      console.log('ERROR', err)
    }
  }

  return { apiOpenAI }
}

export default useApiOpenAI



