import { connect } from 'react-redux'
import ScoreDrawer from './ScoreDrawer'
import {
  getCandidateDetails,
  getCandidateLocation,
  getCandidateFullName
} from 'Shared/selectors/candidates'
import { getSessionData } from 'Shared/helpers/session'

const mapStateToProps = (state, ownProps) => {
  // if (atsDetail == "undefined") return
  const candidateId = getSessionData('atsSelectedCandidateId')

  return ({
    fullName: getCandidateFullName(state, candidateId),
    details: getCandidateDetails(state, candidateId),
    location: getCandidateLocation(state, candidateId)
  })
}

export default connect(mapStateToProps)(ScoreDrawer)



