import React from 'react'
import cx from 'classnames'
import { scoreToFixed } from 'Shared/helpers/ats'
import './ScoreTag.scss'

function ScoreTag (props) {
  if (!props.scoreData) return (<div className={`score-tag score-`}>{props.title}</div>)
  const { title, handleOnClick, scoreData: { score, atsStatus } } = props

  const ATS_STATUS = {
    'failed': 'Score not available',
    'pass': 'pass',
    '-1': 'Pending',
    '-2': 'Score not available'
  }

  if (ATS_STATUS[score] !== undefined) return (<div className={`score-tag`} data-score={scoreToFixed(score, 0)}>{ATS_STATUS[score]}</div>)

  const scoreClass = cx({
    'low': score <= 35,
    'medium': score >= 35 && score < 72,
    'high': score >= 72 && score <= 100
  })

  return (<React.Fragment>
    <div className={`score-tag ${scoreClass}`} onClick={() => handleOnClick()}>
      {scoreClass} {scoreToFixed(score)}
    </div>
  </React.Fragment>)
}

export default ScoreTag



